@import "../../style/local.scss";
.authorization {
  width: 100vw;
  height: 100vh;
  &__wrapper {
    position: absolute;
    top: 175px;
    right: 50%;
    transform: translateX(50%);
    width: 376px;
    animation: slidein 0.5s;
  }
  &__logo {
    @include flex-row-center;
    color: $text-color;
    margin-bottom: 16.5px;
  }

  &__description {
    font-size: 24px;
    line-height: 28px;  
    font-weight: normal;
  }
  &__img {
    margin-right: 11.5px;
  }
  &__content {
    background: $white;
    box-shadow: 0px 1px 0px rgba(90, 97, 105, 0.11),
      0px 2px 4px rgba(90, 97, 105, 0.12), 0px 5px 5px rgba(90, 97, 105, 0.06),
      0px 3.5px 35px rgba(90, 97, 105, 0.1);
    border-radius: 9px;
    padding: 19.5px 17px 20.5px 16px;
  }
  &__heading {
    text-align: center;
    font-size: 17.5px;
    line-height: 20px;
    font-weight: normal;
    letter-spacing: -0.4375px;
    color: $text-color;
    margin-bottom: 35.5px;
  }
  &__inputs {
    @include flex-column;
    height: 114px;
    justify-content: space-between;
    margin-bottom: 15px;
  }

  &__actions {
    @include flex-row-center;
    justify-content: space-between;
  }

  &__access {
    font-weight: normal;
    font-size: 10.5px;
    line-height: 12px;
  }

  &__login {
    width: 110px;
  }
}

@media only screen and (max-width: 767px) {
  .authorization__wrapper {
    width: 100%;
    top: 10px;
    &__content {
      border-radius: 0;
    }
  }
}

@keyframes slidein {
  from {
    top: 0px;
    opacity: 0;
  }
  to {
    top: 175px;
    opacity:1;
  }
}
